import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { UserActionService } from './user-action.service';
import { SigninService } from './signin.service';

@Injectable({
  providedIn: 'root'
})
export class VoteGuard implements CanActivate {
  constructor(private router: Router,private useract: UserActionService,private signin: SigninService) {
  }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

      let p = new Promise((resolve,reject)=>{
        this.signin.loggedIn().subscribe((data: any)=>{
          this.signin.student = data.student;
          resolve();
        },(err)=>{
          reject(err);
        })

      });

      return p.then(()=>{
        return new Promise((resolve,reject)=>{
          this.useract.pollPermission(next.params.id).subscribe((res: any)=>{
            this.useract.election = res.election;
            this.useract.classSize = res.classSize;
            resolve();
          },(err)=>{
            reject(err);
          })
        })
      }).then(()=>{
        return true;
      }).catch((err)=>{
        this.router.navigate(['/login']);
        return false;
      });

  }



}
