import { Component, OnInit, OnDestroy } from '@angular/core';
import { SignupService } from '../signup.service';
import { SigninService } from '../signin.service';
import { Router } from '@angular/router';
import { Login } from '../int/login';
import { Subscription } from 'rxjs';
import { UserActionService } from '../user-action.service';
import { ReCaptchaV3Service } from 'ng-recaptcha';
import { Title,Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  msg: string = "";
  student = <Login>{};
  errorMsg = [];
  loginSub: Subscription;
  isLoading: boolean = false;

  constructor(private signup:SignupService,private signin:SigninService,private router:Router,private useract:UserActionService,
    private recaptcha: ReCaptchaV3Service,private titleService: Title,private metaService: Meta) { }


  ngOnInit() {
    this.titleService.setTitle('Select Polling | Login');
    this.metaService.updateTag({name:'description',content:'Please login to your account. If you do not have one, register now!'});

    if (this.signup.msg != "") {
      this.msg = this.signup.msg;
    }
  }

  ngOnDestroy() {
    this.signup.msg = "";
    if (this.loginSub) {
      this.loginSub.unsubscribe();
    }
    this.useract.blockedAccess = false;
  }

  login(student: Login) {
    this.isLoading = true;
    this.recaptcha.execute('importantAction').subscribe((token) => {
      student.token = token;
      this.loginSub = this.signin.login(student).subscribe(()=>{
        this.router.navigate(['/dashboard']);
      },(err) => {
        this.isLoading = false;
        this.msg = "";
        this.signup.msg = "";
        this.errorMsg = [];
        if (err.status != 423) {
          this.errorMsg = err.error.errors;
        } else {
          this.msg = err.error;
        }

      });


    });





  }

}
