import { Component, OnInit, OnDestroy } from '@angular/core';
import { SignupService } from '../signup.service';
import { Router } from '@angular/router';
import { Registration } from '../int/registration';
import { Subscription } from 'rxjs';
import { ReCaptchaV3Service } from 'ng-recaptcha';
import { Title,Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit, OnDestroy {

  student = <Registration>{}
  errorMsg = [];
  allSchools = ['University of Manitoba'];
  regSub: Subscription;
  isLoading: boolean = false;

  constructor(private signup:SignupService, private router:Router, private recaptcha:ReCaptchaV3Service,private titleService: Title, private metaService: Meta) {}

  ngOnInit() {
    this.titleService.setTitle('Select Polling | Register');
    this.metaService.updateTag({name:'description',content:'A place to run your student council elections securely.'});

    this.student.status = "yes";
    this.student.school = "University of Manitoba";
  }

  ngOnDestroy() {
    if (this.regSub) {
      this.regSub.unsubscribe();
    }
  }

  register(student: Registration): void {
    this.isLoading = true;
    this.recaptcha.execute('importantAction').subscribe((token) => {
      student.token = token;
      this.regSub = this.signup.register(student).subscribe((data) => {
        this.signup.msg = data.msg;
        this.router.navigate(['/login']);
      }, (err) => {
        this.isLoading = false;
        this.errorMsg = err.error.errors;
      });

    });

  }

}
