import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot } from '@angular/router';
import { SigninService } from './signin.service';
import { UserActionService } from './user-action.service';
import { Subscription } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ClasscheckGuard implements CanActivate {

  classCheckSub: Subscription;
  loggedSub: Subscription;

  constructor(private signin: SigninService, private router: Router,private useract: UserActionService) {}

  canActivate(route: ActivatedRouteSnapshot) {

    let p = new Promise((resolve,reject)=>{
      this.loggedSub = this.signin.loggedIn().subscribe((data: any)=>{
        this.signin.student = data.student;
        resolve();
      },(err)=>{
        reject(err);
      })
    });

    return p.then(() => {
      return new Promise((resolve,reject)=>{
        this.classCheckSub = this.useract.statusCheck(route.params.id).subscribe((response: any)=>{
          this.useract.status = response.master;
          this.useract.currentUrl = route.params.id;
          resolve()
        },(err)=>{
          this.useract.blockedAccess = true;
          reject(err)
        });
      })

    }).then(()=>{
      this.classCheckSub.unsubscribe();
      this.loggedSub.unsubscribe();
      return true
    }).catch((error)=>{
      // not logged in
      if (error.status == 422) {
        this.classCheckSub.unsubscribe();
        this.loggedSub.unsubscribe();
        this.router.navigate(['/dashboard']);
        return false
      } else {
        this.loggedSub.unsubscribe();
        this.router.navigate(['/login']);
        return false;
      }

    })


  }

}
