import { Component, OnInit } from '@angular/core';
import{ PasswordReset } from '../int/password-reset';
import { Router } from '@angular/router';
import { UserActionService } from '../user-action.service';

@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
  styleUrls: ['./reset.component.css']
})
export class ResetComponent implements OnInit {

  errorMsg = [];
  user = <PasswordReset>{};
  success: boolean = false;
  isLoading: boolean = false;

  constructor(private useract: UserActionService,private router: Router) { }

  ngOnInit() {
    document.querySelector("title").innerHTML = "Select Polling | Password";
    this.user.forgotPassword = this.useract.fpassword;
    this.user.username = this.useract.username;
  }

  resetPassword() {
    this.isLoading = true;
    this.useract.changeMyPassword(this.user).subscribe(()=>{
      this.isLoading = false;
      this.errorMsg = [];
      this.success = true;
    },(err)=>{
      this.isLoading = false;
      this.success = false;
      this.errorMsg = err.error.errors;
    });
  }

}
