import { Component, OnInit,OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import * as Highcharts from 'highcharts';
import { Meta,Title } from '@angular/platform-browser';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit,OnDestroy {

  john: string = '14%';
  highcharts = Highcharts;
  jane: string = '10%';
  janeInterval: any;
  johnInterval: any;
  timeLeft: any = 22;
  timer: any;
  janeCount: number = 10;
  johnCount: number = 14;
  chartOptions: any = {
    chart: {
        plotBackgroundColor: null,
        plotBorderWidth: null,
        plotShadow: false,
        type: 'pie'
    },
    credits: {
      enabled:false
    },
    title: {
        text: 'Senior Stick Election '
    },
    tooltip: {
        pointFormat: '{series.name}: <b>{point.y}</b>'
    },
    plotOptions: {
        pie: {
            allowPointSelect: true,
            cursor: 'pointer',
            dataLabels: {
                enabled: false
            },
            showInLegend: true
        }
    },
    series: [{
        name: 'Votes',
        colorByPoint: true,
        data: [{name:'Tim Elias',y:7,sliced:true,selected:true},{name:'Lucas Kyle',y:3},{name:'Johnny Apple',y:4}]
    }]
  };

  constructor(private router: Router,private metaService:Meta,private titleService:Title) { }

  ngOnInit() {

    this.titleService.setTitle('Select Polling');
    this.metaService.updateTag({name:'description',content:'A place to run your student council elections securely.'});
    this.metaService.updateTag({name:'keywords',content:'Elections,voting,university,student council'});


    this.johnInterval = setInterval(()=>{
      const re = new RegExp("\\d+");
      let votePCT = parseInt(this.john.match(re)[0]);
      if (votePCT > 50) {
        this.john = "14%";
        this.johnCount = 14;
      } else {
        let newVote = votePCT + 2;
        this.john = newVote + "%";
        this.johnCount = this.johnCount + 2;
      }
    },10000);

    this.janeInterval = setInterval(()=>{
      const re = new RegExp("\\d+");
      let votePCT = parseInt(this.john.match(re)[0]);
      if (votePCT > 50) {
        this.jane = "10%";
        this.janeCount = 10;
      } else {
        let newVote = votePCT + 2;
        this.jane = newVote + "%";
        this.janeCount = this.janeCount +2;
      }

    },15000);

    this.timer = setInterval(()=>{

      this.timeLeft--;
      if (this.timeLeft <= 0) {
        clearInterval(this.janeInterval);
        clearInterval(this.johnInterval);
        clearTimeout(this.timer);
        this.timeLeft = "expired";
      }
    },60000)
  }

  ngOnDestroy() {
    clearInterval(this.janeInterval);
    clearInterval(this.johnInterval);
    clearTimeout(this.timer);
  }

  login() {
    this.router.navigate(['/login']);
  }

  register() {
    this.router.navigate(['/register']);
  }

  electionTypes() {
    this.router.navigate(['/whatis']);
  }

}
