import { Component, OnInit,OnDestroy } from '@angular/core';
import { SigninService } from '../signin.service';
import {ViewChild, ElementRef} from '@angular/core';
import { Router } from '@angular/router';
import { UserActionService } from '../user-action.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit,OnDestroy {

  @ViewChild('delete', {static:false}) trash: ElementRef;
  @ViewChild('close',{static:false}) back: ElementRef;
  @ViewChild('modal',{static:false}) modal: ElementRef;
  user = <any>{};
  deleteDetails = <any>{};
  password: string = "";
  errorMsg = [];
  change: boolean = false;
  master: boolean;
  changePasswordObj = {};
  success: boolean = false;
  setting: string = "";
  isLoadingPass: boolean = false;
  isLoadingDel: boolean = false;
  isLoadingLeaveClass: boolean = false;
  isLoadingDelClass: boolean = false;
  logoutSub: Subscription;
  deleteClassSub: Subscription;
  leaveClassSub: Subscription;
  changePasswordSub: Subscription;
  deleteAccSub: Subscription;

  constructor(private signin: SigninService,private router: Router,private useract: UserActionService) {}

  ngOnInit() {
    this.user = this.signin.student;
    document.querySelector('title').innerHTML = `Select Polling | ${this.user.firstname}`;

  }

  ngOnDestroy() {
    if (this.logoutSub) this.logoutSub.unsubscribe();
    if (this.deleteClassSub) this.deleteClassSub.unsubscribe();
    if (this.leaveClassSub) this.leaveClassSub.unsubscribe();
    if (this.changePasswordSub) this.changePasswordSub.unsubscribe();
    if (this.deleteAccSub) this.deleteAccSub.unsubscribe();
  }

  logout() {
    this.logoutSub = this.signin.logout().subscribe((data)=>{
      this.router.navigate(['/login']);
    },(err)=>{
    });
  }

  deleteClassMaster(i) {
    this.password = ""
    this.master = true;
    this.deleteDetails = this.user.classrooms_master[i];
    this.deleteDetails.index = i;
    this.trash.nativeElement.click();
  }

  deleteClassStudent(i) {
    this.password = "";
    this.master = false;
    this.deleteDetails = this.user.classrooms_student[i];
    this.deleteDetails.index = i;
    this.trash.nativeElement.click();
  }

  removeMaster() {
    this.isLoadingDelClass = true;
    this.deleteClassSub = this.useract.deleteClass(this.password,this.deleteDetails.name).subscribe((data)=>{
      this.back.nativeElement.click();
      window.location.reload();
    },(err)=>{
      this.isLoadingDelClass = false;
      this.success = false;
      this.back.nativeElement.click();
      this.password = "";
      this.errorMsg = err.error.errors;
    })
  }

  removeStudent() {
    this.isLoadingLeaveClass = true;
    this.leaveClassSub = this.useract.leaveClass(this.password,this.deleteDetails.name).subscribe((data)=>{
      this.back.nativeElement.click();
      window.location.reload();
    },(err)=>{
      this.isLoadingLeaveClass = false;
      this.success = false;
      this.back.nativeElement.click();
      this.password = "";
      this.errorMsg = err.error.errors;
    })
  }

  changeSettings(setting) {
    this.setting = setting;
    this.change = true;
  }

  changeMyPassword(pwdObject) {
    this.isLoadingPass = true;
    this.changePasswordSub = this.useract.pwd(pwdObject).subscribe(()=>{
      this.isLoadingPass = false;
      this.errorMsg = [];
      this.success = true;
      this.change = false;
      this.changePasswordObj = {};
    },(err)=>{
      this.isLoadingPass = false;
      this.success = false;
      this.errorMsg = err.error.errors;
    });
  }

  deleteAcc(pwd) {
    this.isLoadingDel = true;
    this.deleteAccSub = this.useract.deleteMyAcc(pwd).subscribe(()=>{
      window.location.reload();
    },(err)=>{
      this.isLoadingDel = false;
      this.password = "";
      this.success = false;
      this.errorMsg = err.error.errors;
    });

  }

}
