import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SignupService } from './signup.service';
import { Login } from './int/login';
import { Observable } from 'rxjs';
import { Student } from './int/student';

@Injectable({
  providedIn: 'root'
})
export class SigninService {

  student: Student;

  constructor(private http: HttpClient,private signup: SignupService) { }

  login(student: Login): Observable<any> {
    return this.http.post("https://www.selectpolling.ca/login",student);
  }

  loggedIn(): Observable<any> {
    return this.http.get("https://www.selectpolling.ca/loggedin");
  }

  logout(): Observable<any> {
    this.signup.msg = "";
    return this.http.get("https://www.selectpolling.ca/logout");
  }

}
