import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router, CanActivate } from '@angular/router';
import { Observable } from 'rxjs';
import { UserActionService } from './user-action.service';
import { Subscription } from 'rxjs';
import { SigninService } from './signin.service';

@Injectable({
  providedIn: 'root'
})
export class ResetGuard implements CanActivate {

  loggedInSub: Subscription;
  accessFPwdView: Subscription;

  constructor(private router: Router,private useract: UserActionService,private signin:SigninService) {

  }

  canActivate(route: ActivatedRouteSnapshot) {

    let p = new Promise((resolve,reject)=>{
      this.loggedInSub = this.signin.loggedIn().subscribe((data: any) => {
        this.signin.student = data.student;
        reject(new Error('Logged in.'));
      },(err)=>{
        resolve();
      });
    });

    return p.then(()=>{
      return new Promise((resolve,reject)=>{
        this.accessFPwdView = this.useract.accessForgotPasswordView(route.params.username,route.params.hash).subscribe((response: any) => {
          this.useract.username = response.username;
          this.useract.fpassword = response.forgotPassword;
          resolve();
        },(error)=>{
          reject(error);
        });
      })

    }).then(()=>{
      this.accessFPwdView.unsubscribe();
      this.loggedInSub.unsubscribe();
      return true;
    }).catch((error) => {
      if (!error.status) {
        this.loggedInSub.unsubscribe();
        this.router.navigate(['/dashboard']);
        return false;
      } else {
        this.loggedInSub.unsubscribe();
        this.accessFPwdView.unsubscribe();
        this.router.navigate(['/login']);
        return false;
      }

    })

  }

}
