import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule }   from '@angular/forms';
import { HighchartsChartModule } from 'highcharts-angular';
import { RECAPTCHA_V3_SITE_KEY, RecaptchaV3Module } from 'ng-recaptcha';


import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';

import { RouterModule, Routes } from '@angular/router';
import { RegisterComponent } from './register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';

import { AuthGuard } from './auth.guard';
import { BypassGuard } from './bypass.guard';
import { ClasscheckGuard } from './classcheck.guard';
import { ResetGuard } from './reset.guard';
import { VoteGuard } from './vote.guard';

import { CreateComponent } from './create/create.component';
import { ProfileComponent } from './profile/profile.component';
import { ClassroomComponent } from './classroom/classroom.component';
import { SearchPipe } from './search.pipe';
import { UserSearchPipe } from './user-search.pipe';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ArchivePipe } from './archive.pipe';
import { ResetComponent } from './reset/reset.component';
import { ManualComponent } from './manual/manual.component';
import { HomeComponent } from './home/home.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { TermsComponent } from './terms/terms.component';
import { WhatisComponent } from './whatis/whatis.component';
import { FppComponent } from './fpp/fpp.component';
import { StvComponent } from './stv/stv.component';
import { ApprovalComponent } from './approval/approval.component';

const appRoutes: Routes = [
  {path:"manual",component:ManualComponent},
  {path:"",component:HomeComponent},
  {path:"privacy",component:PrivacyComponent},
  {path:"terms",component:TermsComponent},
  {path:'login',component:LoginComponent,canActivate:[BypassGuard]},
  {path:"register",component:RegisterComponent,canActivate:[BypassGuard]},
  {path:'dashboard',component:DashboardComponent, canActivate:[AuthGuard]},
  {path:'create',component:CreateComponent, canActivate:[AuthGuard]},
  {path: 'profile',component:ProfileComponent, canActivate:[AuthGuard]},
  {path:'classroom/:id',component:ClassroomComponent, canActivate:[ClasscheckGuard]},
  {path:'reset',component:ForgotPasswordComponent,canActivate:[BypassGuard]},
  {path:'change-pwd/:username/:hash',component:ResetComponent,canActivate:[ResetGuard]},
  {path:"election/fpp/:id",component:FppComponent,canActivate:[VoteGuard]},
  {path:"election/stv/:id",component:StvComponent,canActivate:[VoteGuard]},
  {path:"election/approval/:id",component:ApprovalComponent,canActivate:[VoteGuard]},
  {path:"whatis",component:WhatisComponent},
  {path:'**',redirectTo:'/login',pathMatch: 'full'}
]

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    CreateComponent,
    ProfileComponent,
    ClassroomComponent,
    SearchPipe,
    UserSearchPipe,
    ForgotPasswordComponent,
    ArchivePipe,
    ResetComponent,
    ManualComponent,
    HomeComponent,
    PrivacyComponent,
    TermsComponent,
    WhatisComponent,
    FppComponent,
    StvComponent,
    ApprovalComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    HttpClientModule,
    FormsModule,
    HighchartsChartModule,
    RecaptchaV3Module
  ],
  providers: [AuthGuard,{ provide: RECAPTCHA_V3_SITE_KEY, useValue: '6Ld-1PsUAAAAAAMg_aTSUoaBqNbUnbpo4FPWdYcE' }],
  bootstrap: [AppComponent]
})
export class AppModule { }
